  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-bottom: 1px solid #c6c6c6;
  border-right: 1px solid #c6c6c6;
  font-size: 0.24rem;
  line-height: 0.36rem;
}
.keyboard .keyboard-content .keys .key:nth-child(3n){
  border-right: none;
}
.keyboard .keyboard-content .keys .key:nth-child(n+10){
  border-bottom: none
}
.keyboard .keyboard-content .keys span:nth-of-type(1){
  font-weight: 600;
  font-size: 0.40rem;
}
.active{
  background-color: #aaaa;
}
.empty,.del{
  background-color: #d1d5dc;
}
